.topnavbar{
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    background-color: black;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0px;
}

.sideiconsdiv{
    width: 90px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-right: 10px;
}

#topnaviglogo{
    height: 30px;
    float: left;
}

#liketopbtn{
    height: 25px;
    width: 25px;
}

#igtoplogo{
    height: 35px;
    width: 35px;
    color: white;
    margin-left: 20px;
}

#msgtopicon{
    height: 25px;
    width: 25px;
    color: white;
}